/*************DOCTOR**************/
.aboutDoctor {
  display: flex;
  flex-direction: row;
  @include mainContainerMargins;
  height: 50vh;
}
/******* doctor's description ****/
.doctorTextContainer {
  display: flex;
  text-align: center;
  align-items: center;
  justify-items: flex-start;
  justify-content: flex-start;
  font-size: 20px;
  font-weight: lighter;
  width: 40vw;
  margin-right: 5vw;
}
.doctorText {
  line-height: 2;
  font-size: 16px;
}
.doctorTextHead {
  font-size: 26px;
  font-weight: 500;
  margin-bottom: 1.5vh;
  text-align: left;
}
/******** doctor's picture *****/
.doctorPictureContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.doctorPicture {
  display: flex;
  align-items: center;
  justify-items: center;
  width: 340px;
  height: 360px;
}

/***************** media ********************/
@media only screen and (max-width: 1024px) {
  .doctorTextHead {
    font-size: 20px;
  }
  .doctorText {
    font-size: 14px;
  }
}

@media only screen and (max-width: 812px) {
  .aboutDoctor {
    height: 90vh;
    margin-left: 10vw;
    margin-right: 5vw;
  }
  .doctorPicture {
    width: 240px;
    height: 250px;
  }
  .doctorTextHead {
    font-size: 18px;
  }
  .doctorText {
    font-size: 12px;
    width: 55vw;
  }
}

@media only screen and (max-width: 680px) and (min-height: 450px) {
  .aboutDoctor {
    flex-direction: column;
    height: 70vh;
    margin-right: 10vw;
    margin-left: 4vw;
    width: 100vw;
  }
  .doctorTextContainer {
    width: 90vw;
    margin-bottom: 5vh;
    margin-left: 0vw;
    margin-top: 2vh;
  }
  .doctorText {
    width: 90vw;
    text-align: center;
  }
  .doctorTextHead {
    font-size: 16px;
  }
  .doctorPicture {
    margin-right: 6vw;
  }
}

@media only screen and (max-height: 350px) and (max-width: 600px) {
  .aboutDoctor {
    flex-direction: column;
    height: 150vh;
    width: 100vw;
    margin-left: 3vw;
    margin-top: 2vh;
  }
  .doctorTextContainer {
    width: 90vw;
    margin-top: 2vh;
    margin-bottom: 5vh;
    margin-left: 0vw;
  }
  .doctorText {
    width: 90vw;
    text-align: center;
  }
  .doctorTextHead {
    font-size: 16px;
  }
  .doctorPicture {
    margin-right: 6vw;
  }
}

@media only screen and (max-width: 380px) {
  .aboutDoctor {
    height: 90vh;
  }
}

@media only screen and (max-width: 330px) {
  .aboutDoctor {
    width: 100vw;
    height: auto;
  }
}
